webpackJsonp([139912377317252],{

/***/ 557:
/***/ (function(module, exports) {

	module.exports = {"data":{"site":{"siteMetadata":{"description":"I am Kostas Bariotis, a web developer, a proud wanderer and a passionate doer. My mission is to write clean and efficient code, to solve problems on the web and to learn something more.","siteUrl":"https://kostasbariotis.com"}}},"pathContext":{"posts":[{"excerpt":"Static site generators are amazing tools, hands down! I love working with  Jekyll  and here are some of the features you are missing if you aren't using one: Deduplication of common code blocks Write you code once, include it everywhere Template…","html":"<p>Static site generators are amazing tools, hands down! I love working with <a href=\"https://jekyllrb.com/\" target=\"_blank\" rel=\"noreferrer noopener\">Jekyll</a> and here are some of the features you are missing if you aren't using one:</p>\n<ul>\n<li>Deduplication of common code blocks</li>\n</ul>\n<p>Write you code once, include it everywhere</p>\n<ul>\n<li>Template engine</li>\n</ul>\n<p>You know, for loops, if conditionals, etc</p>\n<ul>\n<li>Separated data stores</li>\n</ul>\n<p>Separation of concerns</p>\n<ul>\n<li>Configuration management</li>\n</ul>\n<p>Generate multiple versions of your site based on the environment or whatever</p>\n<p>I have been using Jekyll for a while now and I have to love it. I wouldn't say that it's the best since I've never used some other but I would love to hear from someone that has features that are missing from Jekyll.</p>\n<p>So on this blog, I would like to present you with my minimal Jekyll configuration that I use on every site I work with. It has the form of a tutorial, so either follows steps below or grab directly the code from GitHub and start coding.</p>\n<h2 id=\"generate-a-new-site\"><a href=\"#generate-a-new-site\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Generate a new site</h2>\n<p>Start by generating a new Jekyll project. Download and install <code>ruby</code>.</p>\n<p><code>jekyll new .</code></p>\n<h2 id=\"file-and-folders-structure\"><a href=\"#file-and-folders-structure\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>File and folders structure</h2>\n<p>At first, you will notice that there is no markup at all. By default, Jekyll uses a theme that hides all that from you. But we need to inject our custom markup. Let's first remove the line <code>theme: minima</code> from _config.yml.</p>\n<p>You will also notice a <code>_posts</code> folder with an example blog post inside. Let's not spend time on that. Maybe in another post. Delete the whole folder.</p>\n<p>Now, open <code>about.md</code> and <code>index.md</code>. Those are the main pages of our site the index page and the about page. Every file you put there, Jekyll will generate a new page with that page in that path.</p>\n<p>About's page content:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-yaml\"><code><span class=\"token punctuation\">---</span>\n<span class=\"token key atrule\">layout</span><span class=\"token punctuation\">:</span> page\n<span class=\"token key atrule\">title</span><span class=\"token punctuation\">:</span> <span class=\"token string\">\"About\"</span>\n<span class=\"token key atrule\">permalink</span><span class=\"token punctuation\">:</span> /about/\n<span class=\"token punctuation\">---</span>\n</code></pre>\n      </div>\n<p>Now, this is Jekyll's frontmatter. We are requesting here this page to be generated with the default layout, have that specific title and be the <code>index.html</code> in the root directory. Jekyll will either copy paste every HTML file as is inside the final folder or will generate HTML files as told. This is what we do here, we are instructing Jekyll to generate an <code>index.html</code> file in the root directory.</p>\n<p>Index page uses the home layout. But what are these layouts? Glad you finally asked. Those layouts were being hidden again by the default time. Let's write our own. Let's have one for start. Create the <code>_layouts/default.html</code> file and put inside:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-xml\"><code><span class=\"token doctype\">&lt;!DOCTYPE html></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>html</span> <span class=\"token attr-name\">lang</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>en<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>head</span><span class=\"token punctuation\">></span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>meta</span> <span class=\"token attr-name\">charset</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>UTF-8<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>meta</span> <span class=\"token attr-name\">name</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>viewport<span class=\"token punctuation\">\"</span></span> <span class=\"token attr-name\">content</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>width=device-width, initial-scale=1.0<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>meta</span> <span class=\"token attr-name\">http-equiv</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>X-UA-Compatible<span class=\"token punctuation\">\"</span></span> <span class=\"token attr-name\">content</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>ie=edge<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>title</span><span class=\"token punctuation\">></span></span>Document<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>title</span><span class=\"token punctuation\">></span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>head</span><span class=\"token punctuation\">></span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>body</span><span class=\"token punctuation\">></span></span>\n{{ content }}\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>body</span><span class=\"token punctuation\">></span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>html</span><span class=\"token punctuation\">></span></span>\n</code></pre>\n      </div>\n<p>This is a basic HTML structure with the <code>{{content}}</code> directive. This directive will render everything from a page that has extend that layout.</p>\n<p>Great! Now we have a layout as well. I don't like HTML files laying around at the root directory. Let's have a separate folder for our pages. Create a <code>pages</code> folder in the root directory. The underscore demotes that this folder will be processed by Jekyll. Now we need an index page. Create an <code>index</code> folder inside <code>_pages</code> with an index.html file inside. Put inside the below snippet:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-yaml\"><code><span class=\"token punctuation\">---</span>\n<span class=\"token key atrule\">layout</span><span class=\"token punctuation\">:</span> default\n<span class=\"token key atrule\">title</span><span class=\"token punctuation\">:</span> <span class=\"token string\">\"WOW Jekyll\"</span>\n<span class=\"token key atrule\">permalink</span><span class=\"token punctuation\">:</span> index.html\n<span class=\"token punctuation\">---</span>\n&lt;h1<span class=\"token punctuation\">></span>Hello world&lt;/h1<span class=\"token punctuation\">></span>\n</code></pre>\n      </div>\n<p>Do the same for the about page. This way, we will keep only configuration files in the root directory and have our content in respective folders.</p>\n<p>Let's proceed with the 404 page. Same thing, create a <code>pages/404/index.html</code> with contents:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-yaml\"><code><span class=\"token punctuation\">---</span>\n<span class=\"token key atrule\">layout</span><span class=\"token punctuation\">:</span> default\n<span class=\"token key atrule\">title</span><span class=\"token punctuation\">:</span> <span class=\"token string\">\"404 Not Found\"</span>\n<span class=\"token key atrule\">permalink</span><span class=\"token punctuation\">:</span> 404.html\n<span class=\"token punctuation\">---</span>\n&lt;h1<span class=\"token punctuation\">></span>Not found&lt;/h1<span class=\"token punctuation\">></span>\n</code></pre>\n      </div>\n<p>And here's our 404 page that our HTTP server will server whenever doesn't find a corresponding route.</p>\n<p>Fire <code>jekyll serve</code> and open the displayed link in your browser. You will see our index page.</p>\n<p>Go to an undefined route and you will see our 404 page.</p>\n<h2 id=\"webpack-integration\"><a href=\"#webpack-integration\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Webpack integration</h2>\n<p>Now that we have our basic file structure, we need a way to handle our frontend assets. I am using NPM for my JS dependencies. Same with CSS dependencies plus the SASS preprocessor. Let's first, create a folder to put all these there. Create a <code>_sources</code> folder and <code>js</code> and <code>sass</code> folders inside.</p>\n<p>Let's first initialize NPM.</p>\n<p><code>npm init</code></p>\n<p>follow the instructions until you are finished.</p>\n<p>Proceed with installing webpack and it's dependencies.</p>\n<p><code>npm i --save webpack style-loader file-loader extract-text-webpack-plugin css-loader postcss-loader webpack-dev-server sass-loader node-sass webpack</code></p>\n<p>Now let's install Bootstrap.</p>\n<p><code>npm i --save bootstrap-sass</code></p>\n<p>We are going to use a few Bootstrap's core modules like the typography and the grid. I am not going to use Bootstrap's JS modules since they require jQuery.</p>\n<p>We will use <code>webpack</code> to process the files from that folder and put them inside <code>assets</code> folder. Jekyll will copy that folder inside the final build.</p>\n<p>Create an <code>webpack.config.js</code> to the root directory. Put there the contents:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-js\"><code><span class=\"token keyword\">const</span> webpack <span class=\"token operator\">=</span> <span class=\"token function\">require</span><span class=\"token punctuation\">(</span><span class=\"token string\">'webpack'</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n<span class=\"token keyword\">const</span> ExtractTextPlugin <span class=\"token operator\">=</span> <span class=\"token function\">require</span><span class=\"token punctuation\">(</span><span class=\"token string\">'extract-text-webpack-plugin'</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n<span class=\"token keyword\">const</span> path <span class=\"token operator\">=</span> <span class=\"token function\">require</span><span class=\"token punctuation\">(</span><span class=\"token string\">'path'</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n\nmodule<span class=\"token punctuation\">.</span>exports <span class=\"token operator\">=</span> <span class=\"token punctuation\">{</span>\n  entry<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">{</span>\n    main<span class=\"token punctuation\">:</span> <span class=\"token template-string\"><span class=\"token string\">`</span><span class=\"token interpolation\"><span class=\"token interpolation-punctuation punctuation\">${</span>__dirname<span class=\"token interpolation-punctuation punctuation\">}</span></span><span class=\"token string\">/source/sass/main.scss`</span></span><span class=\"token punctuation\">,</span>\n    <span class=\"token string\">'index-page'</span><span class=\"token punctuation\">:</span> <span class=\"token template-string\"><span class=\"token string\">`</span><span class=\"token interpolation\"><span class=\"token interpolation-punctuation punctuation\">${</span>__dirname<span class=\"token interpolation-punctuation punctuation\">}</span></span><span class=\"token string\">/source/js/index-page.js`</span></span>\n  <span class=\"token punctuation\">}</span><span class=\"token punctuation\">,</span>\n  output<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">{</span>\n    path<span class=\"token punctuation\">:</span> <span class=\"token string\">\"/path.join(__dirname, ../static/'),\"</span>\n    publicpath<span class=\"token punctuation\">:</span> <span class=\"token string\">\"/'/',\"</span>\n    filename<span class=\"token punctuation\">:</span> <span class=\"token string\">'js/[name].js'</span><span class=\"token punctuation\">,</span>\n  <span class=\"token punctuation\">}</span><span class=\"token punctuation\">,</span>\n  module<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">{</span>\n    loaders<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">[</span>\n      <span class=\"token punctuation\">{</span>\n        test<span class=\"token punctuation\">:</span> <span class=\"token regex\">/\\.scss$/</span><span class=\"token punctuation\">,</span>\n        loader<span class=\"token punctuation\">:</span> ExtractTextPlugin<span class=\"token punctuation\">.</span><span class=\"token function\">extract</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">{</span>\n          fallback<span class=\"token punctuation\">:</span> <span class=\"token string\">'style-loader'</span><span class=\"token punctuation\">,</span>\n          use<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">[</span>\n            <span class=\"token punctuation\">{</span>\n              loader<span class=\"token punctuation\">:</span> <span class=\"token string\">'css-loader'</span><span class=\"token punctuation\">,</span>\n              options<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">{</span>\n                sourceMap<span class=\"token punctuation\">:</span> <span class=\"token boolean\">true</span>\n              <span class=\"token punctuation\">}</span>\n            <span class=\"token punctuation\">}</span><span class=\"token punctuation\">,</span> <span class=\"token punctuation\">{</span>\n              loader<span class=\"token punctuation\">:</span> <span class=\"token string\">'postcss-loader'</span><span class=\"token punctuation\">,</span>\n              options<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">{</span>\n                sourceMap<span class=\"token punctuation\">:</span> <span class=\"token boolean\">true</span><span class=\"token punctuation\">,</span>\n                plugins<span class=\"token punctuation\">:</span> <span class=\"token keyword\">function</span> <span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n                  <span class=\"token keyword\">return</span> <span class=\"token punctuation\">[</span>\n                    <span class=\"token function\">require</span><span class=\"token punctuation\">(</span><span class=\"token string\">'precss'</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">,</span>\n                    <span class=\"token function\">require</span><span class=\"token punctuation\">(</span><span class=\"token string\">'autoprefixer'</span><span class=\"token punctuation\">)</span>\n                  <span class=\"token punctuation\">]</span><span class=\"token punctuation\">;</span>\n                <span class=\"token punctuation\">}</span>\n              <span class=\"token punctuation\">}</span>\n            <span class=\"token punctuation\">}</span><span class=\"token punctuation\">,</span> <span class=\"token punctuation\">{</span>\n              loader<span class=\"token punctuation\">:</span> <span class=\"token string\">'sass-loader'</span><span class=\"token punctuation\">,</span>\n              options<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">{</span>\n                sourceMap<span class=\"token punctuation\">:</span> <span class=\"token boolean\">true</span>\n              <span class=\"token punctuation\">}</span>\n            <span class=\"token punctuation\">}</span>\n          <span class=\"token punctuation\">]</span>\n        <span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">,</span>\n        include<span class=\"token punctuation\">:</span> <span class=\"token template-string\"><span class=\"token string\">`</span><span class=\"token interpolation\"><span class=\"token interpolation-punctuation punctuation\">${</span>__dirname<span class=\"token interpolation-punctuation punctuation\">}</span></span><span class=\"token string\">/source/sass`</span></span><span class=\"token punctuation\">,</span>\n      <span class=\"token punctuation\">}</span>\n    <span class=\"token punctuation\">]</span><span class=\"token punctuation\">,</span>\n  <span class=\"token punctuation\">}</span><span class=\"token punctuation\">,</span>\n  plugins<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">[</span>\n    <span class=\"token keyword\">new</span> <span class=\"token class-name\">ExtractTextPlugin</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">{</span>\n      filename<span class=\"token punctuation\">:</span> <span class=\"token string\">'css/style.css'</span><span class=\"token punctuation\">,</span>\n      allChunks<span class=\"token punctuation\">:</span> <span class=\"token boolean\">true</span><span class=\"token punctuation\">,</span>\n    <span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span>\n  <span class=\"token punctuation\">]</span><span class=\"token punctuation\">,</span>\n  devtool<span class=\"token punctuation\">:</span> <span class=\"token string\">'eval-cheap-module-source-map'</span><span class=\"token punctuation\">,</span>\n  devServer<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">{</span>\n    inline<span class=\"token punctuation\">:</span> <span class=\"token boolean\">true</span><span class=\"token punctuation\">,</span>\n    proxy<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">{</span>\n      <span class=\"token string\">\"**\"</span><span class=\"token punctuation\">:</span> <span class=\"token string\">\"http://localhost:4000\"</span>\n    <span class=\"token punctuation\">}</span><span class=\"token punctuation\">,</span>\n    host<span class=\"token punctuation\">:</span> <span class=\"token string\">'0.0.0.0'</span>\n  <span class=\"token punctuation\">}</span><span class=\"token punctuation\">,</span>\n<span class=\"token punctuation\">}</span><span class=\"token punctuation\">;</span>\n</code></pre>\n      </div>\n<p>So webpack will process our main <code>scss</code> file and put it into <code>assets/css/style.css</code>. Jekyll will be able to grab it from there. Also, JS files will be processed, be bundled and put into <code>assets/js</code> as a separate file for each of our pages.</p>\n<p>Last, we have enabled <code>webpack-dev-server</code> and make it a proxy to Jekyll's host and port. So, in order to use it and have all the good stuff like Hot module replacement we will use that server on development.</p>\n<p>All we need now is to include them into our layout file.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-html\"><code><span class=\"token doctype\">&lt;!DOCTYPE html></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>html</span> <span class=\"token attr-name\">lang</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>en<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>head</span><span class=\"token punctuation\">></span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>meta</span> <span class=\"token attr-name\">charset</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>UTF-8<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>meta</span> <span class=\"token attr-name\">name</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>viewport<span class=\"token punctuation\">\"</span></span> <span class=\"token attr-name\">content</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>width=device-width, initial-scale=1.0<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>meta</span> <span class=\"token attr-name\">http-equiv</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>X-UA-Compatible<span class=\"token punctuation\">\"</span></span> <span class=\"token attr-name\">content</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>ie=edge<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>title</span><span class=\"token punctuation\">></span></span>{{page.title || site.title}}<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>title</span><span class=\"token punctuation\">></span></span>\n  &lt;link rel=\"stylesheet\" href../static/css/main.css\">\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>head</span><span class=\"token punctuation\">></span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>body</span><span class=\"token punctuation\">></span></span>\n{{ content }}\n&lt;script src../static/js/entry.js\"><span class=\"token script language-javascript\"></span><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>script</span><span class=\"token punctuation\">></span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>body</span><span class=\"token punctuation\">></span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>html</span><span class=\"token punctuation\">></span></span>\n</code></pre>\n      </div>\n<p>Fire up webpack's dev server:</p>\n<p><code>./node_modules/.bin/webpack-dev-server --inline --hot</code></p>\n<h2 id=\"npm-scripts\"><a href=\"#npm-scripts\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>NPM Scripts</h2>\n<p>Now that everything is in place, we want to take advantage of webpack's cool dev features like hot module replacement, etc. To do this we need</p>\n<h2 id=\"deployment\"><a href=\"#deployment\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Deployment</h2>","id":"/home/travis/build/kbariotis/kostasbariotis.com/blog/Jekyll + webpack = ❤️/index.md absPath of file >>> MarkdownRemark","timeToRead":6,"frontmatter":{"date":"2017-02-25T00:00:00.000Z","path":"/drafts/jekyll-webpack/","tags":null,"title":"Jekyll + webpack = ❤️","draft":true}},{"excerpt":"Aw man! What a year! So many great experiences, so many mixed feelings! 2016 was full of retrospective meetings. Why not make one for my-self and for the year past. I did set a few goals  last year . What I did Travel more I visited Bucharest…","html":"<p>Aw man! What a year! So many great experiences, so many mixed feelings!</p>\n<p>2016 was full of retrospective meetings. Why not make one for my-self and for the year past. I did set a few goals <a href=\"https://kostasbariotis.com/beyond-sunset/\" target=\"_blank\" rel=\"noreferrer noopener\">last year</a>.</p>\n<h1 id=\"what-i-did\"><a href=\"#what-i-did\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>What I did</h1>\n<ul>\n<li>Travel more</li>\n</ul>\n<p>I visited Bucharest, Romania in June for <a href=\"https://jscamp.ro\" target=\"_blank\" rel=\"noreferrer noopener\">jscamp.ro</a>. Didn't have the time to walk around a lot so I will get there again in any chance given to me. Also, visited Paris for the dotJS/dotCSS conferences. What a magnificent event!</p>\n<ul>\n<li>Read/Write more</li>\n</ul>\n<p>Nah, I can't say I'm satisfied with this one. I was watching a lot of blogs and I was reading Medium a lot. Didn't read all the books I wanted to read. I enjoyed Big Brother and The man and the see. I started Aurelia but never finished it. Zero to One was a great read as expected. Started Lean Analytics a few weeks ago.</p>\n<p>But, I did start a podcast! [http://devastation.tv] started back in February and it's single purpose is learn a lot more and share those learning with my fellow greeks. Much like writing tech blogs posts!</p>\n<p><a href=\"https://kostasbariotis.com/data-migration-with-nodejs/\" target=\"_blank\" rel=\"noreferrer noopener\">\"Data Migrations with Node.js\"</a> was my most ridden piece this year.</p>\n<ul>\n<li>Stay fit</li>\n</ul>\n<p>I did a lot of running and I did notice a lot of improvement over the previous years. I didn't manage to make it my priority and a formal habit though. I still have to convince myself in order to get out of the building and go running. Still room for improvements here.</p>\n<ul>\n<li>Speak to a big conference</li>\n</ul>\n<p>Does being listed in a <a href=\"http://2016.bulgariawebsummit.com/\" target=\"_blank\" rel=\"noreferrer noopener\">big conference's speaker</a> list as a speaker counts for this one? I did manage to get on that list but never made it to the conference. Everything was setup (thank you Bogo for you support, sorry for the mess) perfectly, there were two days left for the conference and I was about to travel by car the next day, until borders closed by Greek truck drivers riots. All three roads that could lead me to Sofia from Thessaloniki were closed. Airlines also do not favor Thessaloniki and I had to fly to Athens in order to land to Sofia. I was very disappointed with this one.</p>\n<p>Nevertheless, I did fly to Athens mid-December to give a talk at GreeceJS, the biggest Javascript meetup in Athens. It was a great experience, met a lof of new people there. <em>Photos soon to come</em></p>\n<p>2016 was also hiding a few other surprises. I left Goodvidio a month ago and now I am in the process of getting a new job. It has been 18 amazing months, full startup mode! Met some brilliant and wonderful guys over there. I am going to miss them for sure.</p>\n<h1 id=\"where-to-go-next\"><a href=\"#where-to-go-next\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Where to go next</h1>\n<p>So, what's on for 2017? Well, I won't include the travel/read/speak/write thing! I want those to be parts of my life but I don't want to keep track of them. I will try this year too and even harder.</p>\n<p>I will try to put a single goal for this year. I have been amazed by Universal Javascript trend that hit the since last year. I think 2017 will be a great year for Isomorphic applications along with progressive apps guidelines. I want this year to get my hands into and build myself an app that will follow all those principles.</p>\n<p>That's my goal for 2017!</p>","id":"/home/travis/build/kbariotis/kostasbariotis.com/blog/and-another-end/index.md absPath of file >>> MarkdownRemark","timeToRead":3,"frontmatter":{"date":"2016-12-25T00:00:00.000Z","path":"/drafts/and-another-end/","tags":null,"title":"And another end","draft":true}},{"excerpt":"Tell me about a situation where you were disagreeing with other people on the team Knowing how to communicate effectively\nvalid arguments\npoints\nknow what your talkign about\ndo your homework/research\nwant to go deeper? fallacies  http://www.nobeliefs…","html":"<p>Tell me about a situation where you were disagreeing with other people on the team</p>\n<ol>\n<li>\n<p>Knowing how to communicate effectively\nvalid arguments\npoints\nknow what your talkign about\ndo your homework/research\nwant to go deeper? fallacies <a href=\"http://www.nobeliefs.com/fallacies.htm\" target=\"_blank\" rel=\"noreferrer noopener\">http://www.nobeliefs.com/fallacies.htm</a></p>\n</li>\n<li>\n<p>Not everything is about you. Everything is about the team. Disagreeing will only slow the whole team down.\nKnow when to hold back. When to say, you know what, I do disagree with all of you, but let's try that and see what happens.</p>\n</li>\n</ol>","id":"/home/travis/build/kbariotis/kostasbariotis.com/blog/disagree-and-commit/index.md absPath of file >>> MarkdownRemark","timeToRead":1,"frontmatter":{"date":"2016-11-24T13:47:24.000Z","path":"/drafts/disagree-and-commit/","tags":null,"title":"Disagree and commit","draft":true}},{"excerpt":"Great, so finished your newly designed REST API that will back up your upcoming platform. You are about to hit live and you are expecting a fair amount of visitors, but there is the possibility of overcoming that amount and you want to test your API…","html":"<p>Great, so finished your newly designed REST API that will back up your upcoming platform. You are about to hit live and you are expecting a fair amount of visitors, but there is the possibility of overcoming that amount and you want to test your API to see how far it can go.</p>\n<p>Enter stress tests. Stress tests can simulate users that come and hit requests over your API. The frequency of those requests is the key metric here and what you really want to know is how much you can afford.</p>\n<p>The most well known stress test utility out there is Apache's AB. If you have installed Apache already you also have installed AB. So fire up your console, write <code>ab</code> and hit Enter.</p>\n<p>AB is benchmarking tool for Apache servers, but you can use it with every HTTP serving platforms, such as Nodejs apps.</p>","id":"/home/travis/build/kbariotis/kostasbariotis.com/blog/rest-api-stress-test/index.md absPath of file >>> MarkdownRemark","timeToRead":1,"frontmatter":{"date":"2016-11-01T00:00:00.000Z","path":"/drafts/rest-api-stress-test/","tags":null,"title":"REST API Stress Test","draft":true}},{"excerpt":"I am Software Engineer being paid two times over the standard Greek wage. But why you say. When I was growing up I had to deal with two things: a) I didn't had the money to do anything I wanted to do and b) I had to do shitty jobs to get that money…","html":"<p>I am Software Engineer being paid two times over the standard Greek wage. But why you say.</p>\n<p>When I was growing up I had to deal with two things: a) I didn't had the money to do anything I wanted to do and b) I had to do shitty jobs to get that money.</p>\n<p>So I had to find a solution. I was introduced to programming and everyone were talking about how is the job of the future. I fall in love with it since my first key strokes. I made my decision at that moment.</p>\n<p>I had a mentor back then. My cousin. 20 years of experience more than me and he really wanted to stack them all in my head. Once he said to me: \"There are two options in life. You can either be a cow, walking around the same grass land your whole life or you can be a hawk, flying and traveling every day and watching the world from the sky. You got choose.\" That completely messed up my childhood. I had to find a way to be hawk.</p>\n<p>I don't like long term goals. Even for my longest goals I will break them into smaller tasks and commit to those.</p>\n<p>My most long term goal was and still is one. Is the one that I set when I was a kid in order to solve my problems.</p>\n<p>To be a high paid professional in my industry until my thirtieth birthday.</p>\n<p>Let's break that down:</p>\n<p>Professional: I love programming and that is the primary reason I do it for living. If I didn't do it for any reason, I would probably doing it in my free time. I wrote my first lines of code in the age of 10. That was it for me. Right there I discovered what I wanted to do in life.</p>\n<p>High paid: Not just paying the bills. I want to be high paid. Have you ever declined a suggestion for a road trip or even to get out for some drinks, cause you couldn't afford it? Yeah, I know. I don't want this. I want money to not be problem. They're supposed to be the ticket, not the barrier.</p>\n<p>The original goal was until the age of 25. I failed, so I rescheduled it. If I were to predict my next deadline, I would say I won't fail this time. Even if I do, I will just set the next goal and move on. I had enough.</p>","id":"/home/travis/build/kbariotis/kostasbariotis.com/blog/the-reason-i-do-what-i-do/index.md absPath of file >>> MarkdownRemark","timeToRead":2,"frontmatter":{"date":"2016-10-20T00:00:00.000Z","path":"/drafts/the-reason-i-do-what-i-do/","tags":null,"title":"The reason I do what I do","draft":true}}],"page":2,"pagesSum":4,"prevPath":"/drafts/page/1","nextPath":"/drafts/page/3"}}

/***/ })

});


